











































import Vue from 'vue';
import Board from './components/Board.vue';

export default Vue.extend({
	components: {
		Board,
	},
	data() {
		return {
			arr: [],
			color: '#000000',
			deleting: false,
			readonly: false,
			grid: true,
			checkers: false,
			slot: false,
			size: 15,
		};
	},
	methods: {
		clear() {
			(this.$refs.board as any).clearBoard();
		},
		
		save() {
			(this.$refs.board as any).downloadImage();
		},
	},
});
